body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  background-color: #f0f2f5;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.App {
  text-align: center;
  padding: 20px;
  max-width: 600px;
  width: 100%;
}

.App-header {
  margin-bottom: 20px;
}

h1 {
  color: #333;
  margin-bottom: 20px;
  font-size: 24px;
  font-weight: bold;
}

.search-box {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}

input[type="text"] {
  width: 300px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 25px 0 0 25px;
  font-size: 16px;
  outline: none;
}

button {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 0 25px 25px 0;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s;
  outline: none;
}

button:hover {
  background-color: #0056b3;
}

.weather-card {
  background: linear-gradient(to bottom right, #4facfe, #00f2fe);
  color: white;
  padding: 20px;
  border-radius: 15px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin-top: 20px;
}

.weather-card p {
  margin: 5px 0;
  font-size: 18px;
}

.weather-icon {
  font-size: 48px;
  margin-bottom: 10px;
}
